import React, { useEffect, useMemo, useState } from 'react';
import axios from 'axios';
import { LowSync, LocalStorage } from 'lowdb';

import Table from './components/table';

import logo from './logo.svg';
import './App.css';

// https://raw.githubusercontent.com/Joystream/founding-members/main/data/fm-info.json

interface Column {
  Header: string;
  accessor: string;
}

interface Member {
  memberHandle: string;
  directScores: number[];
  memberId: number;
  totalDirectScore: number;
  totalReferralScore: number;
  totalScore: number;
}

export default function App() {
  const [rawData, setData] = useState<Member[]>([]);

  useEffect(() => {
    async function fetchData() {
      const result: any = await axios.get(
        'https://raw.githubusercontent.com/Joystream/founding-members/main/data/fm-info.json'
      );
      // console.log(result);
      setData(result.data.scores.totalScores);
    }

    fetchData();
  }, []);

  const columns: Column[] = useMemo(
    () => [
      {
        Header: '#',
        accessor: 'index',
      },
      {
        Header: 'ID',
        accessor: 'memberId',
      },
      {
        Header: 'Member handle',
        accessor: 'memberHandle',
      },
      {
        Header: 'Direct Score',
        accessor: 'totalDirectScore',
      },
      {
        Header: 'Referral Score',
        accessor: 'totalReferralScore',
      },
      {
        Header: 'Total Score',
        accessor: 'totalScore',
      },
      ...rawData[0]?.directScores.map((score, index) => ({
        Header: `Period ${index}`,
        accessor: `period_${index}`,
      })) || [],
    ],
    [rawData]
  );

  const data = rawData ? rawData.map((d:Member) => ({
    ...d,
    ...d.directScores.reduce((prev, curr, index) => ({
      ...prev,
      [`period_${index}`]: curr
    }), {})
  })) : [];

  return (
    <React.StrictMode>
      <Table id="TableTradesHistory" columns={columns} data={data} />
    </React.StrictMode>
  );
}
