import React, { useEffect, useState, PropsWithChildren, ReactElement } from 'react';
import {
  HeaderProps,
  useTable,
  usePagination,
  useSortBy,
  useFilters,
  useGlobalFilter,
  FilterValue,
  IdType,
  Row,
  TableOptions,
  Column,
} from 'react-table';
import cn from 'classnames';

interface Table<T extends object>/*  extends TableOptions<T>  */{
  id: string;
  data: Array<T>;
  columns: Array<Column<T>>;
  fetchData?: Function;
  initSortBy?: Array<{
    id: string;
    desc: boolean;
  }>;
  striped?: boolean;
  pageCount?: Number;
  total?: number;
  getRowProps?: Function;
  showColumnHidding?: boolean;
}

let firstLoad = true;
const handleGetList = (fetchData: Function, filter: any, data: any) => {
  if (!fetchData) {
    return;
  }

  if (filter.sort && filter.sort[0]) {
    filter.sortBy = filter.sort[0].id;
    filter.sortDesc = filter.sort[0].desc;
    delete filter.sort;
  }

  const prms: { [index: string]: any } = {};
  filter.filters.forEach((f: { id: string; value: any }) => {
    prms[f.id] = f.value;
  });

  delete filter.filters;

  if (firstLoad && data && data.length) {
    firstLoad = false;
  } else {
    fetchData({
      ...filter,
      ...prms,
    });
    firstLoad = false;
  }
};

const DefaultHeader: React.FC<HeaderProps<any>> = ({ column }) => (
  <>{column.id}</>
);

function DefaultColumnFilter<T extends object>() {
  return null;
}

const defaultColumn = {
  Filter: DefaultColumnFilter,
  Header: DefaultHeader,
};

export function fuzzyTextFilter<T extends object>(
  rows: Array<Row<T>>,
  id: IdType<T>,
  filterValue: FilterValue
) {
  return null;
}

const filterTypes = {
  fuzzyText: fuzzyTextFilter,
};

const defaultPropGetter = () => ({});

function Table<T extends object> ({
  id,
  columns,
  data,
  fetchData,
  initSortBy = [],
  striped = false,
  total = 0,
  getRowProps = defaultPropGetter,
  showColumnHidding = false,
}: Table<T>): ReactElement {
  const [showColumnHiddingBlock, setShowColumnHiddingBlock] = useState(false);

  const [localPageSize, setLocalPageSize] = useState<number>(100);

  const controlledPageCount = React.useMemo(
    () => Math.ceil(total / localPageSize),
    [total, localPageSize]
  );

  // console.log('table columns', columns);
  // console.log('table data', data);
  

  // Use the state and functions returned from useTable to build your UI
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    allColumns,
    state: { pageIndex, pageSize, sortBy, filters },
  } = useTable<T>(
    {
      columns,
      data,
      initialState: {
        pageIndex: 0,
        pageSize: localPageSize,
        // hiddenColumns: getUserSetting(`Table${id}-hiddenColumns`) || [],
        sortBy: initSortBy
      },
      defaultColumn,
      filterTypes, // TODO: !!!!!!!!!!!!!!!!
      // manualPagination: !!fetchData,
      // manualSortBy: !!fetchData,
      // manualFilters: !!fetchData,
      // autoResetPage: !fetchData,
      // autoResetSortBy: !fetchData,
      pageCount: !!fetchData ? controlledPageCount : undefined,
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  );  

  useEffect(() => {
    if (fetchData) {
      handleGetList(
        fetchData,
        {
          limit: pageSize,
          offset: pageIndex * pageSize,
          sort: sortBy,
          filters,
        },
        data
      );
    }
  }, [fetchData, pageSize, pageIndex, sortBy, filters]);

  useEffect(() => {
    // setUserSetting(`Table${id}-pageSize`, pageSize);
    setLocalPageSize(pageSize);
  }, [pageSize]);

  // Render the UI for your table
  return (
    <>
      {/* {showColumnHidding && (
        <Button
          link
          onClick={(e) => setShowColumnHiddingBlock(!showColumnHiddingBlock)}
        >
          {showColumnHiddingBlock ? '–°–∫—Ä—ã—Ç—å –Ω–∞—Å—Ç—Ä–æ–π–∫–∏' : '–ü–æ–∫–∞–∑–∞—Ç—å –Ω–∞—Å—Ç—Ä–æ–π–∫–∏'}
        </Button>
      )} */}
      {/* {showColumnHidding && showColumnHiddingBlock && (
        <div>
          {allColumns.map((column) => {
            const columnProps = column.getToggleHiddenProps();

            const handleChangeHidden = (e) => {
              const hiddens = getUserSetting(`Table${id}-hiddenColumns`) || [];
              const indx = hiddens.indexOf(column.id);

              if (e.value) {
                delete hiddens[indx];
              } else {
                hiddens.push(column.id);
              }

              // setUserSetting(`Table${id}-hiddenColumns`, hiddens);

              columnProps.onChange(e);
            };

            return (
              <span key={column.id}>
                <Checkbox {...columnProps} onChange={handleChangeHidden}>
                  {column.render('Header')}
                </Checkbox>
                &nbsp; &nbsp; &nbsp;
              </span>
            );
          })}
          <br />
        </div>
      )} */}
      <div className="TableContainer">
        <table
          {...getTableProps()}
          className={cn('Table', {
            'Table': striped,
          })}
        >
          <thead className="TableHead">
            {headerGroups.map((headerGroup: any) => (
              <tr
                {...headerGroup.getHeaderGroupProps()}
                className={cn('TableMod-headRow')}
              >
                {headerGroup.headers.map((column: any) => (
                  <th
                    // {...column.getHeaderProps()}
                    {...column.getHeaderProps()}
                    className={cn('TableMod-headCol')}
                  >
                    <div {...column.getSortByToggleProps()}>
                      {column.render('Header')}
                      <span>
                        {column.isSorted
                          ? column.isSortedDesc
                            ? ' üîΩ'
                            : ' üîº'
                          : ''}
                      </span>
                    </div>
                    <div>
                      {column.canFilter ? column.render('Filter') : null}
                    </div>
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {page.map((row: any, i: any) => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps(getRowProps(row))}>
                  {row.cells.map((cell: any) => {
                    if (cell.column.id === "index") {
                      return <td>{row.index + 1}</td>;
                    }

                    return (
                      <td
                        {...cell.getCellProps()}
                        className={cn('TableMod-col', 'TableMod-col--td')}
                      >
                        {cell.render('Cell')}
                      </td>
                    );
                  })}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
      {/* 
        Pagination can be built however you'd like. 
        This is just a very basic UI implementation:
      */}
      <div className="pagination">
        <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
          {'<<'}
        </button>{' '}
        <button onClick={() => previousPage()} disabled={!canPreviousPage}>
          {'<'}
        </button>{' '}
        <button onClick={() => nextPage()} disabled={!canNextPage}>
          {'>'}
        </button>{' '}
        <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
          {'>>'}
        </button>{' '}
        <span>
          Page{' '}
          <strong>
            {pageIndex + 1} of {pageOptions.length}
          </strong>{' '}
        </span>
        <span>
          | Go to page:{' '}
          <input
            type="number"
            defaultValue={pageIndex + 1}
            onChange={(e) => {
              const page = e.target.value ? Number(e.target.value) - 1 : 0;
              gotoPage(page);
            }}
            style={{ width: '100px' }}
          />
        </span>{' '}
        <select
          value={pageSize}
          onChange={(e) => {
            setPageSize(Number(e.target.value));
          }}
        >
          {[100, 1000].map((pageSize) => (
            <option key={pageSize} value={pageSize}>
              Show {pageSize}
            </option>
          ))}
        </select>
      </div>
    </>
  );
}
export default React.memo(Table);
